'use client';

import { useEffect, useState } from 'react';
import { clientsAPI, Client } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function AdminClientsPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [clients, setClients] = useState<Client[]>([]);

  useEffect(() => {
    if (user?.role !== 'admin') router.push('/dashboard');
  }, [user, router]);

  useEffect(() => { (async () => { const res = await clientsAPI.list(); setClients(res.data); })(); }, []);

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Clients</h2>
        <Button onClick={() => router.push('/dashboard/admin/clients/new')}>New Client</Button>
      </div>
      <Card>
        <CardHeader>
          <CardTitle>All Clients</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b text-left text-sm text-gray-600">
                  <th className="pb-3 font-medium">Name</th>
                  <th className="pb-3 font-medium">Email</th>
                  <th className="pb-3 font-medium">Contact</th>
                </tr>
              </thead>
              <tbody>
                {clients.map(c => (
                  <tr key={c._id} className="border-b">
                    <td className="py-3">{c.name}</td>
                    <td className="py-3 text-sm text-gray-600">{c.email || '—'}</td>
                    <td className="py-3 text-sm text-gray-600">{c.contact || '—'}</td>
                  </tr>
                ))}
              </tbody>
            </table>
            {clients.length === 0 && <p className="text-gray-500 text-center py-8">No clients</p>}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
